:root{
	font-size: 10px;
}

@font-face{
	font-family: GolR;
	src: url("../fonts/golosr.ttf");
}

@font-face{
	font-family: GolM;
	src: url("../fonts/golosm.ttf");
}

@font-face{
	font-family: GolD;
	src: url("../fonts/golosd.ttf");
}

@font-face{
	font-family: GolB;
	src: url("../fonts/golosb.ttf");
}

*,
*:before,
*:after{
	box-sizing: border-box;
}

ul, li{
	padding: 0;
	margin: 0;
	list-style-type: none;
}

body{
	font-family: GolR, sans-serif;
	font-size: 1.6rem;
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	color: #0F1418;
}

iframe{
	border: none;
}

a{
	text-decoration: none;
	transition: .2s linear;
	color: #000;
}

h1, h2, h3, h4, h5, h6, p{
	margin: 0;
}

.scroll{
	overflow: hidden;
}

.button{
	outline: none;
	font-family: GolR, sans-serif;
	font-size: 1.6rem;
	cursor: pointer;
	background: none;
	border-radius: 6px;
	padding: 1.5rem 2.2rem;
	border: 1px solid transparent;
	font-family: 'GolB', sans-serif;
	flex-shrink: 0;
	transition: .2s linear;
}

.green__button{
	color: #fff;
	background: #05C632;
}

.green__button:hover{
	background: #00A627;
}

.input{
	outline: none;
	border: none;
	font-size: 1.6rem;
	padding: .7rem 1.2rem;
	color: #000;
	font-family: GolR, sans-serif;
	resize: none;
}

.input::placeholder{
	color: #c7cbcf;
	transition: .2s linear;
}

.input:focus::placeholder{
	opacity: 0;
}

.mt1{
	margin-top: 1rem;
}

.mt2{
	margin-top: 2rem;
}

.mt3{
	margin-top: 3rem;
}

.mt4{
	margin-top: 4rem;
}

.green{
	color: #05C632;
}

.bold{
	font-family: 'GolB', sans-serif;
}

.medium{
	font-family: 'GolM', sans-serif;
}

.container{
	width: 100%;
	margin: 0 auto;
	max-width: 1180px;
}

// Шапка сайта

.header{
	padding: 2.5rem 0;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background: #fff;
	border-bottom: 1px solid #C3CCD2;
	z-index: 10;
}

.header__inner{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header__logo--inner{
	width: 180px;
	flex-shrink: 0;
	margin-right: 2rem;
}

.header__logo{
	width: 100%;
	height: auto;
}

.header__nav{
	display: flex;
	align-items: center;
	margin-right: 2rem;
}

.header__nav li{
	margin-right: 4rem;
}

.header__nav li:last-child{
	margin-right: 0;
}

.header__nav li a, .header__phone--link{
	font-family: GolD, sans-serif;
}

.header__nav li a:hover, .header__phone--link:hover{
	color: #05C632;
}

.header__mobile--menu, .header__mobile--cross{
	display: none;
	cursor: pointer;
}

// Главный блок

.main{
	padding: 9rem 0;
	background: url("../img/main-bg.jpg") center no-repeat;
	background-size: cover;
	margin-top: 8rem;
}

.main__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.main__content{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.main__text--inner{
	width: 100%;
	max-width: 680px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.main__text--title{
	font-size: 5rem;
	font-family: 'GolB', sans-serif;
}

.main__text--text{
	margin-top: 3rem;
	color: #0F1418;
	font-size: 3.2rem;
	font-family: 'GolM', sans-serif;
}

.main__text--button--inner{
	margin-top: 6rem;
	width: 100%;
	display: flex;
	align-items: center;
}

.main__text--button{
	margin-right: 2rem;
}

.main__text--button--text{
	line-height: 24px;
	color: #0F1418;
	font-family: 'GolM', sans-serif;
}

.main__point--inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
	margin-top: 11rem;
}

.main__point--item{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 20%;
	padding-right: 2rem;
}

.main__point--img--inner{
	width: 60px;
	height: 60px;
}

.main__point--img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.main__point--title{
	margin-top: 3rem;
	font-family: 'GolD', sans-serif;
	font-size: 1.8rem;
}

.main__point--text{
	margin-top: 1.5rem;
}

.main__money{
	width: 100%;
	max-width: 380px;
	border-radius: 10px;
	background: #fff;
	box-shadow: 0px 0px 15px rgba(15, 20, 24, 0.2);
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.main__money--choose{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 1.5rem 2.4rem;
	border-bottom: 1px solid #C3CCD2;
}

.main__money--result{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 3rem 2.4rem;
}

.main__money--text{
	font-size: 1.4rem;
	margin-right: 2rem;
}

.main__money--choose--inner{
	display: flex;
	align-items: center;
}

.main__money--choose--item{
	margin-right: 3rem;
	font-size: 1.4rem;
	color: #797c7e;
	font-family: 'GolD', sans-serif;
	position: relative;
}

.main__money--choose--item.active{
	color: #000;
}

.main__money--choose--item.active:before{
	opacity: 1;
}

.main__money--choose--item:before{
	content: "";
	display: block;
	width: 100%;
	height: 3px;
	border-top-left-radius: 15px;
	border-top-right-radius: 15px;
	background: #05C632;
	position: absolute;
	bottom: -15px;
	left: 0;
	opacity: 0;
	transition: .2s linear;
}

.main__money--choose--item:hover:before{
	opacity: 1;
}

.main__money--choose--item:last-child{
	margin-right: 0;
}

.main__money--result--button{
	width: 100%;
	margin-top: 4rem;
	text-align: center;
}

.main__money--result--box{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 3rem;
}

.main__money--result--box:first-child{
	margin-top: 0;
}

.main__money--result--title{
	color: #797c7e;
	text-transform: uppercase;
	font-size: 1.2rem;
}

.main__money--result--val{
	font-size: 2.4rem;
	font-family: 'GolB', sans-serif;
}

.main__money--result--inner{
	width: 100%;
	display: flex;
	flex-wrap: wrap;
}

.main__money--result--item{
	width: 48%;
	margin-top: 2.5rem;
}

.main__money--slideR,
.main__money--slideD{
	width: 100%;
	margin-top: 2rem;
}

input[type=range] {
  -webkit-appearance: none;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: #DDDEDF;
  border-radius: 15px;
}

input[type=range]::-webkit-slider-thumb {
  height: 24px;
  width: 24px;
  border-radius: 50%;
  background: #05C632;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -9px;
}

// Бизнес

.business{
	padding: 8rem 0;
	background: url("../img/business-bg.jpg") center no-repeat;
	background-size: cover;
}

.business__inner{
	width: 100%;
	max-width: 730px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.business__title--inner{
	display: flex;
	align-items: center;
}

.business__logo{
	width: 188px;
	height: auto;
	flex-shrink: 0;
	margin-left: 4rem;
}

.business__text{
	margin-top: 4rem;
	font-size: 2rem;
}

.business__point--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 4rem;
}

.business__point{
	width: 100%;
	margin-top: 2.5rem;
	display: flex;
	align-items: flex-start;
}

.business__point:first-child{
	margin-top: 0;
}

.business__point--img--inner{
	flex-shrink: 0;
	margin-right: 1rem;
	width: 8px;
	position: relative;
	top: 2px;
}

.business__point--img{
	width: 100%;
	height: auto;
}

.business__point--text--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.business__point--title{
	font-size: 2rem;
	font-family: 'GolD', sans-serif;
}

.business__point--text{
	margin-top: .5rem;
}

.business__total--inner{
	margin-top: 3rem;
	display: flex;
	align-items: center;
}

.business__total--bank{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-right: 5.5rem;
	flex-shrink: 0;
}

.business__total--title{
	font-family: 'GolB', sans-serif;
	font-size: 4.6rem;
	color: #05C632;
	max-width: 360px;
}

.business__total--subtitle{
	margin-top: 0.5rem;
	font-size: 3.6rem;
	font-family: 'GolM', sans-serif;
	color: #05C632;
}

.business__total--text{
	font-size: 3.2rem;
	font-family: 'GolB', sans-serif;
}

// Результаты

.result{
	padding: 5rem 0;
}

.result__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.result__title{
	margin-top: 5rem;
	font-size: 2.4rem;
	color: #05C632;
	font-family: 'GolD', sans-serif;
}

.result__partners--inner{
	margin-top: 1rem;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
}

.result__partners--item{
	margin: 1rem;
	padding: 3rem 2rem;
	width: 23.3%;
	border-radius: 15px;
	border: 2px solid #E1E5E8;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: .2s linear;
}

.result__partners--item:hover{
	border: 2px solid #05C632;
}

.result__partners--img{
	width: 143px;
	height: auto;
}

.result__number--inner{
	width: 100%;
	margin-top: 2.5rem;
	display: flex;
	padding-bottom: 5rem;
	border-bottom: 1px solid #C3CCD2;
	flex-wrap: wrap;
}

.result__number--item{
	padding: 3rem 2rem;
	border-radius: 10px;
	border: 2px dashed #E1E5E8;
	transition: .2s linear;
	width: 18%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-right: 2rem;
}

.result__number--item:last-child{
	margin-right: 0;
}

.result__number--item:hover{
	border: 2px dashed #05C632;
}

.result__text{
	font-family: 'GolD', sans-serif;
}

.result__number--title{
	font-size: 2.4rem;
	font-family: 'GolB', sans-serif;
}

.result__grey--text{
	margin-top: 0.5rem;
	color: #797c7e;
	font-size: 1.4rem;
}

.result__get--inner{
	width: 100%;
	margin-top: 3.5rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.result__get--text{
	font-size: 2rem;
	max-width: 80%;
	font-family: 'GolD', sans-serif;
}

// Развитие компании

.develop{
	padding: 5rem 0;
	background: url("../img/brains-bg.jpg") center no-repeat;
	background-size: cover;
}

.develop__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.develop__content{
	width: 100%;
	display: flex;
	align-items: flex-end;
	margin-top: 4rem;
	padding-bottom: 5rem;
	border-bottom: 2px solid #C3CCD2;
	flex-wrap: wrap;
}

.develop__item{
	width: 23%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	box-shadow: 0px 0px 15px rgba(15, 20, 24, 0.2);
	border-radius: 10px;
	margin-right: 2rem;
}

.develop__text{
	margin-top: 1rem;
}

.develop__text:first-child{
	margin-top: 0;
}

.develop__item:last-child{
	margin-right: 0;
}

.develop__item--top{
	width: 100%;
	padding: 1.7rem;
	background: #05C632;
	border-radius: 10px 10px 0 0;
	color: #fff;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-family: 'GolB', sans-serif;
	font-size: 2.4rem;
}

.develop__item--bottom{
	width: 100%;
	padding: 2rem 0;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	background: #fff;
	border-radius: 0 0 10px 10px;
}

.top__text{
	color: #fff;
	margin-top: 0.5rem;
	font-size: 1.2rem;
	font-family: 'GolR', sans-serif;
}

.develop__item--text{
	padding: 0 2rem 1.5rem 2rem;
	border-bottom: 1px solid #C3CCD2;
}

.develop__box{
	width: 100%;
	margin-top: 2rem;
	display: flex;
	align-items: center;
	padding: 0rem 2rem;
}

.develop__box--img--inner{
	width: 56px;
	height: 56px;
	flex-shrink: 0;
	margin-right: 2rem;
}

.develop__box--img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.develop__box--text--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.develop__box--text--title{
	font-family: 'GolB', sans-serif;
}

.develop__box--text--text{
	margin-top: .5rem;
}

.develop__bottom--text{
	margin-top: 3rem;
	text-align: center;
	font-family: 'GolB', sans-serif;
	padding: 0rem 2rem;
	font-size: 1.8rem;
}

// Мобильное меню

.mobile__menu{
	height: calc(100vh - 75px);
	position: fixed;
	top: 76px;
	left: 0;
	width: 100%;
	background: #fff;
	z-index: 10;
	transition: .4s linear;
	display: flex;
	padding: 2rem 0;
	transform: translateX(-110%);
}

.mobile__menu.active{
	transform: translateX(0);
}

.mobile__menu--inner{
	display: flex;
	width: 100%;
	flex-direction: column;
	align-items: flex-start;
}

.mobile__menu--nav{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.mobile__menu--nav li{
	margin-top: 2rem;
}

.mobile__menu--nav li a{
	margin-top: 2rem;
	font-family: 'GolD', sans-serif;
	font-size: 1.8rem;

}

.mobile__menu--nav li:first-child{
	margin-top: 0;
}

.mobile__menu--phone{
	margin-top: 3rem;
	font-family: 'GolD', sans-serif;
	font-size: 2rem;
}

// Галерея

.gallery{
	padding: 6rem 0;
}

.gallery__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.gallery__content{
	width: 100%;
	margin-top: 5rem;
	display: flex;
	flex-wrap: wrap;
}

.gallery__content--item{
	width: 20%;
	height: 200px;
}

.gallery__content--item--img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}

// Обратная связь

.feedback{
	padding: 5rem 0;
}

.feedback__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.feedback__decor{
	position: absolute;
	bottom: 0;
	right: 0;
	z-index: -1;
}

.feedback__content{
	margin-top: 5rem;
	width: 100%;
	display: flex;
	align-items: flex-start;
	position: relative;
	z-index: 3;
}

.feedback__contact--inner{
	width: 500px;
	display: flex;
	justify-content: center;
	flex-shrink: 0;
	margin-right: 2rem;
}

.feedback__contact{
	width: 280px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.feedback__img{
	width: 100%;
	height: auto;
}

.feedback__name{
	margin-top: 4rem;
	font-family: 'GolB', sans-serif;
	font-size: 2.4rem;
	text-align: center;
}

.feedback__phone{
	margin-top: 3rem;
	font-size: 2rem;
	width: 100%;
	text-align: center;
}

.feedback__email{
	margin-top: 3rem;
	padding-bottom: 2rem;
	border-bottom: 1px solid #E0E0E0;
	width: 100%;
	color: #05C632;
	font-size: 2rem;
	text-align: center;
}

.feedback__social--inner{
	width: 100%;
	margin-top: 2rem;
	display: flex;
	align-items: center;
	justify-content: center;
}

.feedback__social--link{
	margin-right: 2rem;
	width: 56px;
	height: 56px;
}

.feedback__social--link:last-child{
	margin-right: 0;
}

.feedback__social--img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.feedback__form{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.feedback__form--text{
	font-size: 2rem;
}

.feedback__form--content{
	margin-top: 2rem;
	width: 100%;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	align-items: center;
}

.feedback__input{
	padding: 1.5rem 2rem;
	width: 48%;
	margin: 2rem 0 0 0;
	border: 2px solid #E1E5E8;
	border-radius: 6px;
}

.feedback__button{
	border-radius: 6px;
	width: 48%;
	margin-top: 2rem;
}

// Футер

.footer{
	padding: 7rem 0;
	background: #CCCCCC;
}

.footer__inner{
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.footer__item{
	width: 33%;
	padding-right: 5rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.footer__data--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 2rem;
}

.footer__item:last-child{
	padding-right: 0;
}

.footer__logo--inner{
	width: 100%;
	display: flex;
	align-items: center;
}

.footer__copy{
	margin-right: 1rem;
}

.footer__logo{
	width: 200px;
}

.footer__title{
	font-size: 2rem;
	font-family: 'Gold', sans-serif;
}

.footer__link{
	color: #05C632;
}

// Мы с сми

.smi{
	padding: 6rem 0rem 10rem 0rem;
	background: url("../img/smi-bg.jpg") center no-repeat;
}

.smi__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.smi__carousel--inner{
	width: 90%;
	margin: 10rem auto 0 auto;
}

.smi__item{
	margin: 0rem 1rem;
	padding: 2.5rem 2rem;
	background: #fff;
	border-radius: 10px;
	width: 31%;
}

.main__money--valute.active{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.main__money--valute{
	display: none;
}

.smi__img--inner{
	padding: 2rem 0 4rem 0;
	width: 100%;
	height: 100px;
	border-bottom: 1px solid #05C632;
	display: flex;
	justify-content: center;
	align-items: center;
}

.smi__img{
	width: 200px;
	height: auto;
}

.smi__text{
	margin-top: 3rem;
	text-align: center;
}

.smi__arrow{
	width: 24px;
	height: 40px;
	cursor: pointer;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 5;
}

.smi__arrow rect{
	transition: .2s linear;
	fill: #000;
}

.smi__arrow:hover rect{
	fill: #05C632;
}

.smi__prev{
	left: -40px;
}

.smi__next{
	right: -40px;
}

// Команда

.team{
	padding: 6rem 0;
	position: relative;
	z-index: 3;
}

.team__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.team__img-bg{
	position: absolute;
	top: 0;
	z-index: -1;
	right: 0;
	width: 38vw;
}

.team__subtitle{
	margin-top: 2rem;
	font-size: 2rem;
}

.team__main{
	margin-top: 4rem;
	width: 100%;
	display: flex;
	align-items: center;
	max-width: 700px;
}

.team__main--img--inner{
	width: 280px;
	height: auto;
	margin-right: 2rem;
	flex-shrink: 0;
}

.team__main--img{
	width: 100%;
	height: auto;
}

.team__main--text--inner{
	width: 100%;
