// Как инвестировать

.how{
	padding: 5rem 0;
	background: #05C632;
}

.how__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.white__text{
	color: #fff;
}

.how__content{
	width: 100%;
	margin: 5rem 0;
	display: flex;
	align-items: flex-start;
}

.how__item{
	width: 25%;
	padding-right: 2rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.how__number{
	width: 75px;
	height: 75px;
	border-radius: 50%;
	border: 1px solid #fff;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: 'GolB', sans-serif;
	font-size: 3.6rem;
	position: relative;
	z-index: 3;
}

.how__number:before{
	content: "";
	width: 300%;
	height: 1px;
	border: 1px dashed #fff;
	position: absolute;
	top: 50%;
	left: 75px;
	transform: translateY(-50%);
	z-index: -1;
}

.how__item:last-child .how__number:before{
	display: none;
}

.how__title{
	color: #fff;
	margin-top: 3rem;
	font-size: 2.4rem;
	font-family: 'GolB', sans-serif;
}

.how__text--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.how__text{
	margin-top: 1rem;
	color: #fff;
}

.how__anket--inner{
	width: 100%;
	padding: 3rem 0;
	border-top: 1px solid #fff;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.how__anket--text{
	font-size: 2rem;
	font-family: 'GolD', sans-serif;
	color: #fff;
}

.how__anket--button{
	background: #fff;
	color: #05C632;
}

.how__anket--button{
	padding: 1.5rem 3rem;
	border: 1px solid #fff;
}

.how__anket--button:hover{
	color: #fff;
	background: #05C632;
}

