// Как зарабатывают инвесторы

.inw{
	padding: 6rem 0;
}

.inw__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.inw__content{
	width: 100%;
	margin: 3rem 0 0 0;
	padding-bottom: 5rem;
	border-bottom: 1px solid #C3CCD2;
	display: flex;
	align-items: flex-start;
}

.inw__text--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.inw__carousel--inner{
	width: 100%;
	max-width: 680px;
	flex-shrink: 0;
	margin-left: 2rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	border-radius: 10px;
	box-shadow: 0px 0px 15px rgba(15, 20, 24, 0.2);
}

.inw__text{
	font-size: 2rem;
}

.inw__point--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 5rem;
}

.inw__point{
	width: 100%;
	display: flex;
	align-items: center;
	margin-top: 3rem;
}

.inw__point:first-child{
	margin-top: 0;
}

.inw__img--inner{
	flex-shrink: 0;
	margin-right: 3rem;
	width: 56px;
}

.inw__img{
	width: 100%;
	height: auto;
}

.inw__point--text{
	font-size: 1.8rem;
}

.inw__carousel--top{
	width: 100%;
	padding: 1.5rem 2rem;
	border-radius: 10px 10px 0 0;
	background: #05C632;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.inw__carousel--title{
	font-family: 'GolB', sans-serif;
	font-size: 2.4rem;
	color: #fff;
	text-align: center;
}

.inw__carousel--text{
	font-size: 1.4rem;
	margin-top: 0.5rem;
	color: #fff;
	text-align: center;
}

.inw__carousel--bottom{
	width: 100%;
}

.inw__carousel--item{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-bottom: 1rem;
}

.inw__carousel--box{
	width: 100%;
	padding: 1.5rem 5rem;
	border-bottom: 1px solid #E0E0E0;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.inw__carousel--box:last-child{
	border-bottom: 0;
}

.inw__view--inner{
	width: 30%;
	display: flex;
	align-items: center;
}

.inw__view--img--inner{
	width: 46px;
	height: 46px;
	flex-shrink: 0;
	margin-right: 1rem;
}

.inw__view--img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.inw__view--text--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.inw__view--text{
	text-transform: uppercase;
	margin-top: 0.5rem;
	color: #797c7e;
	font-size: 1.3rem;
}

.inw__view--text:first-child{
	margin-top: 0;
}

.inw__month--item{
	width: 17.5%;
	padding: 0rem 1rem;
	text-align: center;
	color: #797c7e;
	font-family: 'GolD', sans-serif;
	font-size: 1.8rem;
}

.inw__black--text{
	font-family: 'GolD', sans-serif;
	color: #0F1418;
}

.inw__scroll--item:focus{
	outline: none!important;
}

.inw__content .slick-dots{
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	bottom: -30px;
	left: 50%;
	transform: translateX(-50%);
}

.inw__content .slick-dots li button{
	display: none;
}

.inw__content .slick-dots li{
	width: 16px;
	height: 16px;
	border-radius: 50%;
	border: 1px solid #05C632;
	cursor: pointer;
	margin-right: 1.5rem;
}

.inw__content .slick-dots li:last-child{
	margin-right: 0;
}

.inw__content .slick-dots li.slick-active{
	background: #05C632;
}

.inw__arrow{
	width: 40px;
	height: 15px;
	cursor: pointer;
	position: absolute;
	top: -45px;
	z-index: 5;
}

.inw__arrow path{
	transition: .2s linear;
	fill: #fff;
}

.inw__prev{
	left: 40px;
}

.inw__next{
	right: 40px;
}

// Гарант

.garant{
	padding: 8rem 0;
	position: relative;
}

.garant__inner{
	width: 100%;
