}

.garant__point--item:hover path{
	fill: #05C632;
}

.garant__point--item--text{
	font-family: 'GolD', sans-serif;
}

.garant__point--item--icon{
	flex-shrink: 0;
	margin-right: 2rem;
}

.garant__point--item--icon path{
	transition: .2s linear;
}

