		margin-top: 0;
	}

	// Обратная связь

	.feedback__content{
		flex-direction: column;
		align-items: center;
	}

	.feedback__form{
		margin-top: 3rem;
	}

	.feedback__contact--inner{
		margin-right: 0;
	}

	.feedback__contact--inner{
		width: 100%;
	}

	// Сми

	.smi__arrow{
		transform: translateY(0);
		bottom: -60px;
		top: auto;
	}

	.smi__prev{
		left: 30px;
	}

	.smi__next{
		right: 30px;
	}

	.smi__carousel--inner{
		width: 100%;
	}

	// Команда

	.team__item{
		width: 100%;
		padding-right: 0;
	}

	.team__main{
		flex-direction: column;
		align-items: center;
	}

	.team__main--img--inner{
		margin-right: 0;
	}

	.team__main--text--inner{
		margin-top: 2rem;
	}

	// Как зарабатывают инвесторы

	.inw__carousel--top{
		padding: 1.5rem 7rem;
	}

	.inw__arrow{
		top: -55px;
	}

	.inw__prev{
		left: 25px;
	}

	.inw__next{
		right: 25px;
	}

	.inw__carousel--inner{
		max-width: 100%;
	}

	.inw__scroll--item{
		overflow-x: auto;
	}

	.inw__carousel--item{
		width: 800px;
	}

	.inw__carousel--title{
		font-size: 2rem;
	}

	// Отзывы

	.reviews__item.one{
		background: url("../img/onem.png") center no-repeat;
	}

	.reviews__item.two{
		background: url("../img/twom.png") center no-repeat;
	}

	.reviews__item.three{
		background: url("../img/threem.png") center no-repeat;
	}

	.reviews__item{
		padding: 7rem 11rem;
	}

	.reviews__item--name, .reviews__item--subname,
	.reviews__item--text{
		position: relative;
		top: -25px;
	}

	.reviews__title{
		font-size: 2.8rem;
	}

	.garant__item--box{
		flex-direction: column;
		align-items: flex-start;
		margin: 3rem 0rem 0rem 0rem;
	}
}

@media(max-width: 480px){
	.container{
		max-width: 300px;
	}

	// Шапка

	.header__phone--link{
		display: none;
	}

	// Главный блок

	.main__money--result--item{
		width: 100%;
	}

	.main .main__text--title{
		font-size: 2.5rem;
		color: #fff;
	}

	.main .main__text--text{
		font-size: 2rem;
		color: #fff;
	}

	.main__point--item{
		width: 100%;
		padding-right: 0;
	}

	.main .main__text--button--text{
		color: #fff;
	}

	.main{
		background: url("../img/main-bg-mobile.jpg") center no-repeat;
		background-position-y: 0;
	}

	.main__text--title{
		font-size: 2.5rem;
	}

	// Бизнес

	.business__title--inner{
		flex-direction: column;
		align-items: flex-start;
	}

	.business__logo{
		margin: 2rem 0rem 0rem 0;
		width: 140px;
	}

	.business__total--title{
		font-size: 3rem;
	}

	// Результаты

	.result__partners--item, .result__number--item{
		width: 100%;
	}

	// Галерея

	.gallery__content--item{
		width: 50%;
	}

	.gallery{
		padding: 3rem 0;
	}

	// Обратная связь

	.feedback, .develop{
		padding: 3rem 0;
	}

	.feedback__contact{
		width: 100%;
	}

	.feedback__content{
		margin-top: 2rem;
	}

	// Развитие

	.develop__content{
		margin-top: 0;
	}

	// Команда

	.team__item--img--inner{
		width: 95px;
	}

	.team__main--img--inner{
		width: 100%;
	}

	// Отзывы

	.reviews__item{
		padding: 7rem 3rem;
	}

	.reviews__ava--item{
		margin-right: 2rem;
		width: 70px;
		height: 70px;
	}

	.reviews__ava--item.active{
		width: 90px;
		height: 90px;
	}

	.reviews__title{
		font-size: 2.3rem;
	}

	.reviews{
		padding: 3rem 0;
	}
}